body {
  margin: 0;
  font-family: "Courier New", 'Courier New', Courier, monospace;
  background: linear-gradient(89deg, #200035 20%, #1a82e6 180%);
  background: url(/smartbrain-face-detection-full-stack/static/media/2644830.59d7e774.jpg) no-repeat fixed center;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.center{
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.Tilt{
    background: linear-gradient(89deg, #cf0fbf 0%, #0e0027 60%);
}
.buttoncolor {
  background: linear-gradient(89deg, #200035 30%, #1a82e6 180%);
}

.form{
    width: 700px;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
.lottie-container {
    background-color: transparent;
    border: none;
    overflow: hidden;
  }

  .loading-animation {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  
